@media (max-width: 1550px) {
    .order {
        &__wrapper {
            flex-wrap: wrap;
        }
        &__delivery {
            padding: 40px;
            max-width: 70%;
        }
        &__info {
            padding: 40px;
            max-width: 30%;
        }
        .item {
            padding: 15px;
        }
    }
    .slider {
        &__item {
            &-price {
                display: flex;
            }
        }
    }
}

@media (max-width: 1400px) {
    .order {
        .title__h2 {
            font-size: 22px;
        }
        .title__h3 {
            font-size: 18px;
        }
        .descr {
            font-size: 16px;
        }
        .link {
            font-size: 17px;
        }

        &__info {
            padding: 30px;
            .summary,
            .discount,
            .prediscount {
                &__text {
                    font-size: 18px;
                }
                &__count {
                    font-size: 18px;
                }
            }
        }
    }
    .slider__item {
        height: 130px;
        justify-content: space-between;
        &-price {
          font-size: 18px;  
        }
        &-address {
            max-width: 300px;
        }
    }

    .date {
        .check {
            &__label {
                font-size: 18px;
            }
        }
    }

    .recipient .select select {
        font-size: 18px;
    }
    .recipient .select__descr {
        font-size: 14px;
    }
    .recipient__info-select:not(:last-child) {
        margin-right: 12.5px;
    }
}

@media(max-width: 1200px) {
    .recipient__info-select:not(:last-child) {
        margin-top: 14px;
    } 
}

@media (max-width: 991px) {
    .order {
        &__delivery {
            max-width: 100%;
            border-right: none;
        }
        &__info {
            display: none;
        }

        .title__h2 {
            font-size: 21px;
        }
        .title__h3 {
            font-size: 17px;
        }
        .descr {
            font-size: 14px;
        }
    }

    .slider {
        &__item {
            max-width: 100%;
            height: 116px;
        }
    }

    .bottom {
        &__container {
            flex-wrap: wrap;
        }
        &__price {
            order: 1;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &__info {
            justify-content: initial;
            &-descr {
                display: none;
            }
            &-mobile {
                display: block;
                font-weight: 700;
                font-size: 14px;
                color: #333333; 
            }
            &-price {
                font-weight: 700;
                font-size: 14px;
            }
            &-link {
                display: block !important;
            }
        }
    }
    .bottom__btn {
        margin-top: 16px;
        width: 100%;
        text-align: center;
        font-size: 17px;
        order: 2;
    }
}

@media (max-width: 768px) {
    .recipient {
        .select {
            margin-bottom: 14px;
            &__phone {
                margin-right: 15px;
            }
            &__fullname {
                width: 100%;
            }
            select {
                font-size: 17px;
            }
        }
    }
}

@media (max-width: 576px) {
    .slick-prev,
    .slick-next {
        display: none !important;
    }
    .order {
        &__title {
            font-size: 27px;
            padding-left: 16px;
        }
        &__delivery {
            padding: 24px 15px;
        }
        .item {
            padding: 16px 20px;
        }
    }
    .date {
        &__wrapper {
            flex-wrap: wrap;
        }
        &__other {
            margin-left: 0;
            margin-top: 12px;
        }
        &__options {
            margin-top: 24px;
        }
        .check__label {
            display: block;
        }
    }
    .recipient .select__new-user, .recipient .select__new-phone {
        margin: 0;
    }
    .recipient {
        .select {
            margin: 0;
            &__new-user {
                margin-top: 0;
                position: absolute;
                top: 3px;
                right: 0;
            }
        }
    }
    .phone__wrapper {
        margin-top: 14px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .bottom {
        &__container {
            padding: 0 16px;
        }
        &__btn {
            padding: 21px;
        }
    }
}